body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}


header {
  padding: 1em;
  text-align: center;
}

nav {
  margin: 0;
  padding: 0;
  background-color: #333;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 10px;
}

nav ul li a {
  color: white;
  text-decoration: none;
}

.container {
  padding: 10px 250px 10px;
  background-color: #f8f8f8;

}

form {
  padding: 15px 195px 15px;
  border-radius: 8px;
  max-width: 300px;
  margin: 0 auto;
}

form input {
  width: 100%;
  padding: 0.5em;
  margin-bottom: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
}

form button {
  background-color: #4CAF50;
  color: white;
  padding: 0.5em;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

form button:hover {
  background-color: #45a049;
}