/* App.css */
.App {
  text-align: center;
}

.main-content {
  display: flex;
  justify-content: center;
  padding: 10px;
  margin-top: -50px; /* to account for the sticky navbar */
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}